.search-section {
  transform: translate(0, -50%);
}

.form {
  background-color: var(--white);
  padding: 0 5%;
}

.form__group {
  padding: 0.8rem 0;
}

.form__group--location,
.modal__group--location {
  background-image: url("../../assets/icons/icon-location.svg");
  background-size: 16px;
}

.form-control {
  padding: 0.4rem 0.75rem;
}

.dark-mode .form-control {
  color: var(--white);
}

.form-control:focus,
.form-control:focus-visible,
.form-control:focus-within {
  border: none;
  box-shadow: none;
  outline: none;
}

.form__filter-button,
.form__search {
  background-size: 19px;
  height: 40px;
  width: 40px;
}

.dark-mode ::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: rgb(240, 240, 240);
  opacity: 0.7; /* Firefox */
}

.dark-mode :-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: rgb(240, 240, 240);
  opacity: 0.8;
}

.dark-mode ::-ms-input-placeholder {
  /* Microsoft Edge */
  color: rgb(240, 240, 240);
  opacity: 0.8;
}

.modal__controllers-container {
  padding: 1rem 5%;
}

.form__filter-button {
  background-image: url("../../assets/icons/icon-filter.svg");
}

.home-button:focus,
.home-button:focus-visible,
.home-button:focus-within {
  box-shadow: none;
}

.modal__search,
.form__search {
  background-color: var(--violet);
}

.form__search {
  background-image: url("../../assets/icons/icon-search.svg");
}

.form__search:focus,
.form__search:focus-visible,
.form__search:focus-within {
  background-color: var(--violet);
}

.modal-content {
  left: 3%;
  right: 3%;
  top: 18vh;
  width: 94%;
}

.dark-mode .modal-content {
  background-color: var(--very-dark-blue);
}

.modal-header {
  padding: 1rem 5% 0 5%;
  border-bottom: none;
}

.dark-mode .btn-close {
  filter: invert(1);
}

.modal__form {
  padding: 1rem 0;
}

.modal__group--location {
  background-position: left 5% center;
  border-bottom: 1px solid #979797ab;
  padding: 0.5rem 5%;
}

.modal__location {
  margin-left: 5%;
}

.modal__controllers-container {
  padding: 1rem 5%;
}

.modal__search {
  padding: 0.55rem 0.75rem;
}

.modal__checkbox,
.form-check-label {
  font-weight: 700;
  transition: background 1s ease-in;
}

.dark-mode .form-check-label {
  color: var(--white);
}

.load-more,
.error__button {
  background-color: var(--violet);
  margin: 1.2rem auto 0 auto;
}

.load-more {
  width: 80%;
}

.error-section {
  margin-top: 4rem;
}

.error__title {
  font-size: 2.1rem;
}

.error__button {
  width: 60%;
}

@media screen and (min-width: 576px) {
  .form {
    padding: 0 3%;
  }
  .form__group--title {
    width: 70%;
  }

  .form__search {
    background-image: none;
    width: auto;
  }

  .load-more {
    width: 40%;
  }

  .error-section {
    margin-top: 7rem;
  }

  .error__title {
    font-size: 2.4rem;
  }

  .error__button {
    width: 30%;
  }
}

@media screen and (min-width: 768px) {
  .form__group {
    border-right: 1px solid #979797a3;
    width: 32%;
  }

  .form__group:last-child {
    border-right: none;
    width: 36;
  }

  .form__group {
    background-position: left center;
  }

  .form__group--title {
    background-image: url("../../assets/icons/icon-search-violet.svg");
    background-size: 20px;
  }

  .form-control {
    font-size: 0.95rem;
    margin-left: 16px;
    width: 85%;
  }

  .form-check-label {
    font-size: 0.8rem;
  }
}

@media screen and (min-width: 992px) {
  .form__group {
    padding: 1rem 0;
  }

  .load-more {
    width: 30%;
  }

  .error-section {
    margin-top: 10rem;
  }
}

@media screen and (min-width: 1200px) {
  .form__group {
    padding: 1.4rem 0;
  }

  .load-more {
    width: 20%;
  }

  .error__title {
    font-size: 2.8rem;
  }

  .error__button {
    width: 25%;
  }
}

@media screen and (min-width: 1400px) {
  .error__button {
    width: 15%;
  }
}
